nav.navbar{padding:0;margin:none;height:80px}nav.navbar .container-fluid{padding:0 40px;display:flex;height:inherit}nav.navbar .container-fluid .header-logo{height:28px}nav.navbar{background:none}nav.navbar .container-fluid{background:none}nav.navbar .container-fluid .header-logo{opacity:.9;transition:all 300ms;-webkit-transform:scale(0.92);transform:scale(0.92)}nav.navbar .container-fluid .header-logo:hover{opacity:1;-webkit-transform:scale(1);transform:scale(1)}nav.navbar .container-fluid a.nav-link{color:#eee;opacity:.8;transition:all 300ms;margin-left:10px}nav.navbar .container-fluid a.nav-link:hover{color:#eee;opacity:1}
.portfolio-wrapper{width:98%;margin-left:1%;margin-right:1%}.portfolio-wrapper stackgrid span{margin-bottom:5px;position:relative;top:0;left:0;height:380px}.portfolio-wrapper stackgrid span div.tile{margin-bottom:5px;position:relative;top:0;left:0;min-height:380px;height:auto}.portfolio-wrapper stackgrid span div.content{position:absolute;top:0;left:0;padding:40px;min-height:380px;height:auto}.portfolio-wrapper stackgrid span div.tile{opacity:.2}
body{margin:0;font-size:16px;font-family:"Rubik",sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}body p{line-height:normal;margin-top:18px;margin-bottom:18px;line-height:1.5em}body b{font-weight:500}body h1{font-size:1.3rem}body h2{font-size:.8rem;text-transform:uppercase;letter-spacing:.07em}body{background:#d10000;color:#eee}body p{opacity:.7}body .light{opacity:.5}body h2{opacity:.5}label{display:block;margin-top:10px}.card-container.card{max-width:350px !important;padding:40px 40px}.card{background-color:#f7f7f7;padding:20px 25px 30px;margin:0 auto 25px;margin-top:50px;border-radius:2px;box-shadow:0px 2px 2px rgba(0,0,0,.3)}.profile-img-card{width:96px;height:96px;margin:0 auto 10px;display:block;border-radius:50%}body,html{height:100%;background-color:#d10000}
